import React from "react";
import Modal from "../Modal/modal";
import PayBtn from "../PayBtn/payBtn";
import { MDBJumbotron, MDBCardBody, MDBCardText, MDBCardTitle } from "mdbreact";

const Jumbotron = () => (
  <>
    <section>
      <MDBJumbotron className="text-center mx-auto p-3 my-5">
        <MDBCardBody>
          <MDBCardTitle>Регистрации и&nbsp;оплата участия</MDBCardTitle>
          <p className="default-color-text my-4 font-weight-bold">Участие в&nbsp;Олимпиаде&nbsp;&mdash; добровольное и&nbsp;платное</p>
          <p>Стоимость участия в Олимпиаде - 3 бел. рубля</p>
          <MDBCardText>
            Для участия в&nbsp;Олимпиаде необходимо ознакомится с у&nbsp;Инструкцией
          </MDBCardText>
          <hr className="my-4" />
          <div className="pt-2">
            <Modal />
            <PayBtn />
          </div>
        </MDBCardBody>
      </MDBJumbotron>
    </section>
  </>
)

export default Jumbotron